<template>
  <form @submit="onSubmit" class="m-4 p-4 add-form">
    <div class="form-control">
      <label>Task</label>
      <input type="text" v-model="text" name="text" placeholder="Add Task" />
    </div>
    <div class="form-control">
      <label>Day & Time</label>
      <input
        type="text"
        v-model="day"
        name="day"
        placeholder="Add Day & Time"
      />
    </div>
    <div class="form-control form-control-check">
      <label>Set Reminder</label>
      <input type="checkbox" v-model="reminder" name="reminder" />
    </div>

    <Button class="bg-blue-500" text="Save Task" icon="save" />
  </form>
</template>

<script>
import Button from "./Button.vue";
export default {
  name: "AddTask",
  components: {
    Button,
  },
  data() {
    return {
      text: "",
      day: "",
      reminder: false,
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      if (!this.text) {
        alert("Please add task");
        return;
      }

      const newTask = {
        text: this.text,
        day: this.day,
        reminder: this.reminder,
      };

      console.log("add", newTask);
      this.$emit("add-task", newTask);

      this.text = "";
      this.day = "";
      this.reminder = false;
      //   return;
    },
  },
};
</script>

